export default {
	data() {
		return {

		}
	},
	methods: {
		back(url) {
			uni.switchTab({
				url: url
			})
		},
		jump(url){
			uni.navigateTo({
				url:url
			})
		},
		outlogin(){
			uni.removeStorageSync('token');
			uni.removeStorageSync('member');
			/*---清除洗车服务的缓存数据---*/
			uni.removeStorageSync('wasCarServ');
			/*---清除洗车服务的缓存数据---*/
			setTimeout(()=>{
				uni.navigateTo({
					url:"/pages/login/login"
				})
			},1000);
		}
		
		
	}

}
